// 商品类型管理相关接口
import axios from '../fetch'

// 商品类型列表分页
export const productTypePage = params => { return axios.get('/admin/store/productType/page', { params: params }) }
// 商品类型列表不分页
export const productTypeList = params => { return axios.get('/admin/store/productType/list', { params: params }) }
// 商品类型详情
export const productTypeGet = params => { return axios.get('/admin/store/productType/get', { params: params }) }
// 商品类型删除
export const productTypeDelete = params => { return axios.post('/admin/store/productType/delete', params) }

// 商品类型新增
export const productTypeSave = params => { return axios.post('/admin/store/productType/save', params) }

// 商品类型修改
export const productTypeUpdate = params => { return axios.post('/admin/store/productType/update', params) }

// 规格属性列表

// 商品规格属性列表分页
export const productStandardPage = params => { return axios.get('/admin/store/producttypeattr/page', { params: params }) }

// 商品规格属性详情
export const productStandardGet = params => { return axios.get('/admin/store/producttypeattr/get', { params: params }) }
//  商品规格属性删除
export const productStandardDelete = params => { return axios.post('/admin/store/producttypeattr/delete', params) }

//  商品规格属性新增
export const productStandardSave = params => { return axios.post('/admin/store/producttypeattr/save', params) }
//  商品规格属性修改
export const productStandardUpdate = params => { return axios.post('/admin/store/producttypeattr/update', params) }

// 通过商品类型获取到商品规格属性列表
export const productStandardValue = params => { return axios.get('/admin/store/producttypeattr/list', { params: params }) }




